.section-h1 {
    font-weight: bold;
    font-size: 36px;
    line-height: 110%;

    @media screen and (max-width: $w1376) {
        font-size: 28px;
    }
}

.section-h2 {
    font-size: 24px;
    line-height: 110%;
    padding: rem(26) 0;

    @media screen and (max-width: $w680) {
        font-size: 18px;
    }

}

.section-h3 {
    font-weight: bold;
    font-size: rem(15);
    line-height: 110%;

    @media screen and (max-width: $w1376) {
        font-size: 14px;
    }
}

h1 {
    font-size: rem(36);

    @media screen and (max-width: 1440px) {
        font-size: rem(30);
    }

    @media screen and (max-width: $w760) {
        font-size: rem(26);
    }
}