.personal-layout {
    background: #fff;
    display: flex;
    border-left: 1px solid #ddd;
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    height: 100%;


    @media screen and (max-width: $w940) {}

    &__aside {
        width: 100%;
        max-width: 300px;
        min-width: 260px;
        overflow-y: auto;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: $w940) {
            position: fixed;
            transform: translateX(-100%);
            z-index: 10;
            background: #fff;
            top: 0;
            bottom: 0;
            transition: 0.3s ease;
            left: 0;

            &.open {
                transform: translateX(0%);
            }
        }
    }

    &__content {
        width: 100%;
        flex-grow: 1;
    }

    &__aside {
        border-right: 1px solid #ddd;
    }
}

.personal-area {

    display: flex;
    flex-direction: column;
    height: 100%;

    &__header {
        height: rem(90);
        min-height: rem(90);
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ddd;
        padding: 0 rem(30);

        @media screen and (max-width: $w580) {
            height: rem(70);
            min-height: rem(70);

            display: none;
        }
    }

    &__main {
        flex-grow: 1;
        position: relative;
        padding: 0 2rem;
        height: 100%;
        overflow-y: auto;

        @media screen and (max-width: $w480) {
            padding: 0 1rem;
        }
    }

    &__action {
        background-color: #f4f4f4;
        padding: 0 rem(30);
    }
}

.box-orders {
    max-width: 850px;
    padding: rem(30) 0
}

.page-title {

    height: 70px;
    display: flex;
    align-items: center;

    & h1 {
        font-size: rem(24);
        line-height: 160%;
    }
}

.state-tooltip {
    width: rem(16);
    height: rem(16);
    display: inline-block;
    background-image: url('../img/svg/ic_help.svg');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    position: relative;
    top: rem(3);


    & a {
        border: none;
    }
}