.bln-add-parking {
    &__address {
        display: flex;
        align-items: center;

        & span {
            font-size: rem(14);
        }

        & svg {
            fill: $red;
            width: rem(11);
            height: rem(15);
            margin-right: rem(8);
        }
    }

    &__props {
        padding-top: rem(15);
        padding-left: rem(18);
        padding-bottom: rem(15);
    }

    &__btn {
        display: flex;
        justify-content: center;
    }
}

.bln-confirm-parking {
    &__main {
        display: flex;

        @media screen and (max-width: $w480) {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__image {

        width: 36%;
        min-width: rem(170);

        @media screen and (max-width: $w580) {
            width: rem(170);
            min-width: rem(170);
        }

        @media screen and (max-width: $w480) {
            width: 100%;
            margin-bottom: rem(25);
        }

        & label {

            cursor: pointer;

            & input {
                display: none;
            }

            & span {

                @include bgimage-cover;
                padding-top: 65%;
                background-color: #f8f8f8;
            }
        }

        & .bgimage {
            @include bgimage-cover;
            padding-top: 65%;
        }
    }

    &__history {
        @include text-style-gray;
        text-align: center;
        padding-top: rem(7);
        width: 100%;
        display: block;
        text-decoration: underline;
        cursor: pointer;
    }

    &__data {
        margin-left: rem(20);
    }

    &__address {
        display: flex;
        align-items: center;

        & span {
            font-size: rem(14);
        }

        & svg {
            fill: $red;
            width: rem(11);
            height: rem(15);
            margin-right: rem(8);
        }
    }

    &__props {
        padding-top: rem(15);
        padding-left: rem(18);
        padding-bottom: rem(15);
    }

    &__phone {
        display: flex;
        align-items: center;

        & svg {
            width: rem(20);
            height: rem(20);
            margin-right: rem(7);
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: rem(35);


        @media screen and (max-width: $w480) {
            flex-direction: column;
        }

        &>* {
            margin: 0 7px;

            @media screen and (max-width: $w480) {
                width: 100%;
                margin: 0;
                margin-top: 7px;

                &:first-child {
                    margin-top: 0;
                }

                & .btn {
                    width: 100%;
                    max-width: rem(250);
                    margin: 0 auto;
                }
            }
        }
    }

    &__yn {
        display: flex;
        align-items: center;
        padding-top: rem(12);
        padding-bottom: rem(12);
        background-color: #f8f8f8;
        margin-top: rem(20);

        padding-left: rem(15);
        padding-right: rem(5);

        @media screen and (max-width: $w480) {
            flex-direction: column;
            justify-content: center;
        }

    }

    &__yn-txt {
        margin-right: auto;
        font-size: rem(16);

        @media screen and (max-width: $w480) {
            text-align: center;
            margin-left: auto;
            margin-bottom: 10px;
        }
    }

    &__yn-btn {
        display: flex;
        align-items: center;
        justify-content: center;

        @media screen and (max-width: $w480) {
            width: 100%;
        }

        &>* {
            margin: 0 7px;

            @media screen and (max-width: $w480) {
                width: 50%;
            }

        }

        & .btn {
            max-width: rem(85);
            min-width: rem(50);
            max-height: rem(34);

            @media screen and (max-width: $w480) {
                max-width: 100%;
                min-width: 100%;
                max-height: rem(45);
                width: 100%;
            }
        }
    }

    &__info {
        @include text-style-gray;
        padding-top: rem(15);
        font-size: rem(13);
        text-align: center;
    }
}

.bln-activate-parking {

    max-width: rem(420);

    &__title {
        font-size: rem(18);
        color: #000000;
        text-align: center;
    }

    &__desc {
        padding: rem(10) 0;
        text-align: center;
        max-width: rem(340);
        margin: 0 auto;
        font-size: rem(14);
    }

    &__form {
        max-width: rem(340);
        margin: 0 auto;
    }

    & .form-text {
        @include text-style-gray;
        padding-bottom: rem(10);
        font-size: rem(14);
        text-align: center;
        margin: 0 auto;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: rem(10);

        & .btn {
            min-width: rem(160);
        }

        &>* {
            margin: 0 6px;
        }
    }

    &__info {
        @include text-style-gray;
        padding-top: rem(15);
        font-size: rem(13);
        text-align: center;
    }
}