.register-page {
    height: 100vh;
    position: relative;


    &__bg {
        height: 100vh;
        background-size: cover;
        background-position: center;
    }

    &__content {
        @include layer(1);
        overflow-y: auto;
        display: flex;
        align-items: center;
    }
}

.valid-rules {
    font-size: 12px;
    line-height: 180%;
    color: #838383;
    margin-bottom: rem(10);
    margin-left: rem(5);

    @media screen and (max-width: $w480) {
        font-size: 11px;
    }

    & li {
        &.active {
            color: #00a711;
        }
    }
}

.page-loaded .sh-modal-800,
.page-loaded .sh-balloon,
.page-loaded .fancybox-content,
.page-loaded .sh-modal-tooltip,
.page-loaded .sh-modal {

    animation: .3s slide-up;
    opacity: 1;
}

.sh-modal-800,
.sh-modal-tooltip,
.sh-modal {
    background: #fff;
    max-width: rem(440);
    width: 100%;
    margin: auto;
    padding: rem(40) rem(45);
    border-radius: rem(20);
    opacity: 0;

    @media screen and (max-width: $w480) {
        &.fancybox-content {
            margin-bottom: 0;
        }
    }



    @media screen and (max-width: $w480) {
        padding: rem(30) rem(25);
        width: 100%;
        max-width: 100%;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        margin-bottom: 0;

    }

    @media screen and (max-width: $w480) and (min-height: 620px) {
        &:not(.fancybox-content) {
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

}

.sh-modal-tooltip {
    max-width: rem(580);
}

.sh-modal-800 {
    max-width: rem(800);
}