.confirm-email {
    &__logo {

        margin-top: rem(-15);

        & img {
            max-width: rem(135);
            margin: 0 auto;
        }
    }

    &__title {
        font-size: rem(24);
        line-height: 160%;
        text-align: center;
    }

    &__subtitle {
        font-size: rem(14);
        line-height: 140%;
        text-align: center;
        color: #838383;
    }

    &__title,
    &__button,
    &__subtitle,
    &__form {
        margin-top: rem(20);

    }




}

.resend-code {
    margin-top: rem(15);
    margin-bottom: rem(25);
    width: 100%;

    &__timer,
    &__button {
        font-size: 12px;
        line-height: 140%;
        color: #838383;
        text-align: center;
        width: 100%;

        & a {
            font-size: 12px;
            line-height: 140%;
            color: $red;
            text-decoration: underline;
        }
    }

    &__button {
        display: none;
    }
}